import React, { Component } from 'react';

import '../styles/Pokemon.css';

class Pokemon extends Component {
  render() {
    const { pokemonProps } = this.props;
    return (
      <div className="pokemon-container">
        <div className="pokemon-list">
          <p>Nome: {pokemonProps.name}</p>
          <p>Tipo: {pokemonProps.type}</p>
          <p>Peso: {pokemonProps.averageWeight.value}{pokemonProps.averageWeight.measurementUnit}</p>
          <a rel="noreferrer" target="_blank" href={pokemonProps.moreInfo}><button class="more">Mais Info</button></a>
        </div>
        <img className="avi" src={pokemonProps.image} alt={pokemonProps.name}></img>
      </div>
    );
  }
}

export { Pokemon };
